import { useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core';

// material UI textField     
const useStyles = makeStyles({
	input: {
		'& input + fieldset': {
			borderColor: (props) => props.color,
		},
		'& input:valid:focus + fieldset': {
			borderColor: (props) => props.color,
		},
		'& input:valid:hover + fieldset': {
			borderColor: (props) => props.color,
		},
	},
	helperText: {
		color: (props) => props.color,
	},
});

//  validation 
export const checkPassword = (password) => {
	let res = false;

	if (!password) {
		res = false;
	} else {
		const check = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/.test(password);
		check ? (res = true) : (res = false);
	}

	return res;
};

// Email validation 
export const checkEmail = (email) => {
	let res = false;

	if (!email) {
		res = false;
	} else {
		const check =
			/^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/.test(
				email
			);
		check ? (res = true) : (res = false);
	}

	return res;
};

//
export const useCheckWebsite = (website) => {
	let color = '';
	let message = '';
	let code = '';
	const res =
		/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)/.test(
			website
		);
	if (!website) {
		color = 'rgba(0, 0, 0, 0.3)';
		message = '';
		code = 'empty';
	} else {
		if (res) {
			color = 'green';
			message = 'URL  .';
			code = 'success';
		} else {
			color = 'red';
			message =
				'URL   . (http://  https:// .)';
			code = 'error';
		}
	}

	return {
		input: useStyles({ color }).input,
		helperText: useStyles({ color }).helperText,
		helperTextMessage: message,
		code,
	};
};

export const useCheckDisplayName = (prevDisplayName, displayName) => {
	const exist = useSelector((state) => state.users.checkDisplayName.exist);

	let code = '';
	let color = '';
	let message = '';

	if (prevDisplayName === displayName) {
		code = 'default';
	} else {
		if (!displayName) {
			code = 'empty';
		} else if (!exist[1] || displayName !== exist[1]) {
			//         input   
			code = 'warning';
		} else {
			//    
			if (exist[0]) {
				code = 'error';
			} else {
				//     
				code = 'success';
			}
		}
	}

	switch (code) {
		case 'empty':
			color = 'orange';
			message = '  ';
			break;
		case 'warning':
			color = 'orange';
			message = '  .';
			break;
		case 'success':
			color = 'green';
			message = `${exist[1]}  .`;
			break;
		case 'error':
			color = 'red';
			message = `${exist[1]}   .`;
			break;
		default:
			color = 'rgba(0, 0, 0, 0.3)';
			message = ``;
	}
	return {
		input: useStyles({ color }).input,
		helperText: useStyles({ color }).helperText,
		helperTextMessage: message,
		code,
	};
};
