import { debounce } from 'lodash';
import React, { useRef, useEffect, useMemo } from 'react';

const UseInfiniteScroll = ({ execute }) => {
	const debounceExecute = useMemo(() => debounce(execute, 300), [execute]);

	// Target
	const lastElRef = useRef(null);

	// observer  (useEffect   useRef   )
	const observer = useRef(null);

	useEffect(() => {
		//  Observer    
		if (observer.current) {
			observer.current.disconnect();
			/*
       ,  disconnect     
            
      (execute             )
      */
		}

		// Observer 
		observer.current = new IntersectionObserver(
			([{ isIntersecting }]) => {
				if (isIntersecting) {
					debounceExecute();
				}
			},
			{ threshold: 0.5 }
		);

		// Observer On
		observer.current.observe(lastElRef.current);
	}, [debounceExecute]);

	useEffect(() => {
		// Observer Off
		return () => {
			observer.current.disconnect();
		};
	}, []);

	return <div ref={lastElRef} style={{ height: '20px' }}></div>;
};

export default UseInfiniteScroll;
