import './Menu.scss';

// menu  UI Component
const Menu = ({ children, toggle, isOn, location }) => {
	return isOn ? (
		<div className="menu">
			<div className="menu_background" onClick={toggle}></div>
			<div className={`menu_container ${location}`}>{children}</div>
		</div>
	) : (
		<></>
	);
};

export default Menu;

/* 
children :   className menu_item  
isOn : Menu    true  false   
toggle : isOn true  false   
location : menu_container      
container  top, left, right, bottom   className 
*/
